.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.725rem;
  max-width: 100%;
}

.navbar-hamburger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  z-index: 1001;
  width: 3.125rem;
  height: 3.125rem;
  border: 0.188rem solid #f2b872;
  border-radius: 0.313rem;
}

.navbar-hamburger-line {
  width: 2.5rem;
  height: 0.188rem;
  background-color: #f2b872;
  margin: 0.35rem 0;
  transition: 0.5s;
}

.navbar-hamburger.opened {
  background-color: #f2b872;
}

.navbar-hamburger.opened .navbar-hamburger-line:nth-child(1) {
  transform: translateY(1.1rem) rotate(45deg);
  width: 3.125rem;
  background-color: #fff;
}
.navbar-hamburger.opened .navbar-hamburger-line:nth-child(2) {
  opacity: 0;
}
.navbar-hamburger.opened .navbar-hamburger-line:nth-child(3) {
  transform: translateY(-1.1rem) rotate(-45deg);
  width: 3.125rem;
  background-color: #fff;
}

.navbar-overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 100%;
  background-color: #262626;
  overflow-x: hidden;
  transition: 0.5s;
  z-index: 1000;
  border-radius: 50% 0 0 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.navbar-links {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.navbar-links a {
  text-decoration: none;
  color: #f2b872;
  font-size: 3.25rem;
}

.navbar-overlay.opened {
  width: 100%;
  border-radius: 0;
}

.contact-us {
  width: 15rem;
  height: 4rem;
  background-color: #f2b872;
  border: none;
  border-radius: 0.25rem;
  margin-top: 3rem;
  font-size: 2.125rem;
  font-weight: bold;
  color: #fff;
}
