.about {
  max-width: 100%;
  padding: 0.725rem;
  background-color: #262626;
  position: relative;
}

.shadow {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 5rem;
  margin: 0;
  color: #fff;
  opacity: 10%;
  margin-left: 0.725rem;
}

.about-heading {
  font-size: 2.265rem;
  margin: 0;
  color: #fff;
  margin-top: 1.9rem;
  z-index: 10;
  position: absolute;
}

.about .text-decoration {
  margin-top: 5.25rem;
}

.about-paragraph {
  color: #fff;
  opacity: 40%;
  font-size: 0.875rem;
}

.about-benefits {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin-top: 3rem;
}

.about-benefit {
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #fff;
  opacity: 80%;
}

.about-image {
  width: 100%;
  margin-bottom: 0;
  margin-top: 1rem;
}

.about-image img {
  width: 100%;
  object-fit: contain;
}
