.hero {
  max-width: 100%;
  padding: 0.725rem;
}

.hero-text {
  margin-top: 3rem;
}

.hero-subheading {
  font-family: "Rajdhani", sans-serif;
  font-weight: light;
  font-size: 1.75rem;
  margin: 0;
  color: #f2b872;
}

.hero-heading {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 3.5rem;
  line-height: 2.75rem;
  letter-spacing: 18;
}

.text-decoration {
  width: 6.25rem;
  height: 0.313rem;
  background-color: #f2b872;
  margin-top: 1rem;
}

.hero-paragraph {
  font-size: 0.875rem;
  opacity: 60%;
  padding-right: 0.313rem;
}

.highlight {
  color: #f2b872;
  font-weight: bold;
  font-size: 1rem;
  opacity: 100%;
}

.hero-image {
  width: 100%;
  margin-bottom: -1.125rem;
  margin-top: -2rem;
}
.hero-image img {
  width: 100%;
  object-fit: contain;
}
